.App-header {
  background-color: #000000;
  min-height: 100vh;
  height: auto;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.tasks-container {
  margin-top: 100px;
  width: 620px;
  padding: 20px;
  background-color: #2b2a2a;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.box-add-tasks {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.input-text {
  width: 350px;
  height: 40px;
  padding-left: 10px;
  font-size: 1.3rem;
  background-color: #1f1e1e;
  border: none;
  border-radius: 7px;
  color: #fff;
}

.input-time {
  background-color: #1f1e1e;
  color: #fff;
  border: none;
  height: 35px;
  width: 80px;
  padding: 5px;
  border-radius: 7px;
}

.btn-action {
  display: grid;
  place-items: center;
  background-color: #1f1e1e;
  color: #fff;
  border: none;
  border-radius: 7px;
  height: 40px;
  width: 40px;
  font-size: 2rem;
  font-weight: 600;
  cursor: pointer;
  
}

.btn-action svg {
  width: 100%;
  height: 100%;
}

.container-tasks {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.task-box {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #1f1e1e;
  border-radius: 7px;
}

.task-box .name-task {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 10px;
  color:#fff;
}

.task-box .time-task {
  width: 20%;
  height: 100%;
  display: grid;
  place-items: center;
  color: #fff;

}

.btn-tasks {
  height: 100%;
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.btns {
  border: none;
  background-color: #2b2a2a;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  cursor: pointer;
  transition: all .5s;

  &:hover {
    transition: all .2s;
    background-color: #fff;
    
  }

  &:hover svg {
    color: #2b2a2a;
  }
}

.btns svg{
  color: #fff;
}

.btns-container {
  display: flex;
  gap: 10px;
}

.completed {
  background-color: rgb(0, 116, 0) !important;
}

.late {
  background-color: rgb(116, 14, 0);
}


@media (max-width: 900px) {
  .tasks-container {
    width: 80%;
  }

  .box-add-tasks {
    display: flex;
    flex-wrap: wrap;
  
    gap: 10px;
  }

  .task-box .name-task {
    width: 35%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

}